html {
  display: flex;
}

html,
body {
  display: flex;
  scroll-behavior: smooth;
}

body.sideNavBody { // any body class found on every page can be used here, this keeps styles from effecting the Wordpress editor
  min-height: 100vh;
  width: 100%;
  max-width: 100%;
  flex: 1 1 auto;
  display: flex;
  flex-flow: column nowrap;

  @include hd {
    max-width: 2540px;
  }
}

.wrap {
  flex: 1 1 auto;
  overflow: hidden;
}

html,
body {
  font-size: $body-font-size;
  font-weight: $body-font-weight;
  font-family: $body-font;
  color: $body-color;
  line-height: 1.6;

  @include smooth-fonts;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
.h1 {
  color: $heading-color;
  font-family: $heading-font;
  font-weight: $h1-weight;
  font-size: $h2-size;
  text-transform: uppercase;

  @include tablet {
    font-size: $h1-size;
  }
}

h2,
.h2 {
  color: $heading-color;
  font-family: $heading-font;
  font-weight: $h2-weight;
  font-size: $h3-size;
  text-transform: uppercase;

  @include tablet {
    font-size: $h2-size;
  }
}

h3,
.h3 {
  color: $heading-color;
  font-family: $heading-font;
  font-weight: $h3-weight;
  font-size: $body-font-size;

  @include tablet {
    font-size: $h3-size;
  }
}

p,
li {
  font-family: $body-font;
  font-size: $body-font-size;
  font-weight: $body-font-weight;
}

a {
  transition: $base-transition;
  color: $brand-primary;

  &:hover,
  &:focus {
    color: $brand-hover;
    text-decoration: none;
  }
}

ul.nav a {
  display: block;
}

.panel {
  padding: $section-padding 0;

  @include tablet {
    padding: ($section-padding + 10px) 0;
  }

  @include laptop {
    padding: ($section-padding + 20px) 0;
  }

  @include desktop {
    padding: ($section-padding + 30px) 0;
  }
}

.container {
  @include wide {
    max-width: 1170px;
  }

  @include hd {
    max-width: 1170px;
  }
}

.jumbo-bg {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

br {
  &.mobile {
    display: none;

    @include mobile {
      display: inline;
    }
  }

  &.phablet {
    display: none;

    @include phablet {
      display: inline;
    }
  }

  &.tablet {
    display: none;

    @include tablet {
      display: inline;
    }
  }

  &.laptop {
    display: none;

    @include laptop {
      display: inline;
    }
  }

  &.desktop {
    display: none;

    @include desktop {
      display: inline;
    }
  }
}

.orange {
  color: $brand-orange;
}

.cardstyle {
  background-color: #fff;
  padding: 15px;
  box-shadow: 0 0 15px 0 rgba(0,0,0,0.1);
  margin-bottom: 15px;

  @include tablet {
    padding: 25px;
  }

  &-no-padding {
    padding: 0;
  }

  &-top {
    padding-bottom: 0;
    margin-bottom: 0;

  }

  &-bottom {
    padding-top: 0;
  }
}



